@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
body{
  background: #f0f0f0;
}
.wrapper{ 
  max-width: 650px;
  width: 100%;
  background: #fff;
  margin: 10px auto;
  box-shadow: 1px 1px 2px rgba(0,0,0,0.125);
  padding: 30px;
}

.wrapper .title{
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 25px;
  color: #515AE0;
  text-transform: uppercase;
  text-align: center;
}

.wrapper .form{
  width: 100%;
}

.wrapper .form .inputfield{
  margin-bottom: 15px;
  display: flex;
  align-items: center;
}

.wrapper .form .inputfield label{
   width: 200px;
   color: #000000;
   margin-right: 10px;
  font-size: 16px;
}
.wrapper .form .inputfield img{
  margin: auto;
}
.wrapper .form .inputfield input[type=file]{
  width: 100%;
  font-size: 15px;
  padding: 8px 10px;
}

.wrapper .form .inputfield .input,
.wrapper .form .inputfield .textarea{
  width: 100%;
  outline: none;
  border: 1px solid #d5dbd9;
  font-size: 15px;
  padding: 8px 10px;
  border-radius: 3px;
  transition: all 0.3s ease;
}

.wrapper .form .inputfield .input-uid{
  width: 70%;
}

.wrapper .form .inputfield .textarea{
  width: 100%;
  height: 70px;
  resize: none;
}


.wrapper .form .inputfield .input:focus,
.wrapper .form .inputfield .textarea:focus,
.wrapper .form .inputfield .custom_select select:focus{
  border: 1px solid black;
}


.wrapper .form .inputfield .btn{
    width: 150px;
    padding: 8px 10px;
    font-size: 15px; 
    border: 0px;
    color: #fff;
    cursor: pointer;
    border-radius: 5px;
    outline: none;
    background-color: #515AE0;
    outline: none;
    height: 35px;     
    text-transform: uppercase;
    font-weight: 400;
    transition: 0.5s;
    margin:auto;
}
.wrapper .form .inputfield .btn-fetch{
  width: 28%;
  margin-left: 10px;
}

      
.wrapper .form .inputfield .btn:hover{
    background-color: #4d84e2;
    background-position: right;
}

.wrapper .form .inputfield:last-child{
  margin-bottom: 0;
}

@media (max-width:420px) {
  .wrapper .form .inputfield{
    flex-direction: column;
    align-items: flex-start;
  }
  .wrapper .form .inputfield label{
    margin-bottom: 5px;
  }
  .wrapper .form .inputfield.terms{
    flex-direction: row;
  }
  .wrapper .form .inputfield .btn-fetch{
    margin: auto;
    margin-top: 10px;
    width: 150px;

  }
  .wrapper .form .inputfield .btn{
    margin-top: 10px;
  }
  .wrapper .form .inputfield .input-uid{
    width: 100%;
  }
}